* {
  color: #fff;
  box-sizing: border-box;
  font-family: "Press Start 2P";
}

html, body {
  height: 100%;
  margin: 0;
  overflow: hidden;
}

body {
  background-image: url("background.99285dc6.png");
  background-position: bottom;
  background-size: auto 100%;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 16px;
  display: flex;
}

@media (prefers-color-scheme: dark) {
  body {
    background-image: url("background_night.e0225171.png");
  }
}

#seed {
  text-align: center;
  background: none;
  border: none;
  font-size: min(10vw, 3rem);
  animation: showup 2s cubic-bezier(1, 0, .5, 1);
}

@keyframes showup {
  from {
    transform: scale(2)translateY(300%);
  }
}

#canvas-container {
  width: 100%;
  max-width: 800px;
  border: 1px solid #fff;
  flex: auto;
  animation: fadeIn 2s cubic-bezier(1, 0, .5, 1);
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

